.c-input-box input {
  width: 100%;
  font-size: 16px;
  letter-spacing: 1px;
  color: #fff;
  padding: 10px 20px;
  border-radius: 35px;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  border: none;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  outline: none;
}
.c-input-box input::placeholder {
  color: #fff;
}
.c-input-box input[type=submit] {
  background: #fff;
  color: #666;
  max-width: 100px;
  cursor: pointer;
  margin-bottom: 20px;
  font-weight: 600;
}
.c-input-box.text input {
  border-radius: 20px;
  padding-bottom: 90px;
}

.forget {
  margin-top: 5px;
  color: #fff;
}
.forget a {
  color: #fff;
  font-weight: 600;
}

.c-light {
  position: absolute;
  opacity: 0.5;
  left: 0%;
  width: 100%;
  height: 100%;
  background: radial-gradient(at 50% 0, rgba(229, 255, 0, 0.8), transparent);
  z-index: 30;
  animation: over 15s ease forwards;
}

@keyframes over {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}
.c-linear-light {
  position: relative;
  width: 300px;
  height: 3px;
}
.c-linear-light::before, .c-linear-light::after {
  content: "";
  position: absolute;
}
.c-linear-light:before {
  display: block;
  width: 100%;
  height: 100%;
  bottom: -50px;
  transform-origin: 0 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.4) 80%, white);
  border-radius: 50%;
  animation: line 7s infinite;
}

@keyframes line {
  0% {
    transform: scaleX(0);
    opacity: 0.5;
  }
  90% {
    transform: scaleX(1);
    opacity: 1;
  }
  100% {
    transform: scaleX(1);
    opacity: 0;
  }
}
@keyframes light {
  0% {
    transform: scale(0, 0) translateX(52px);
    opacity: 1;
  }
  100% {
    transform: scale(2, 2) translateX(52px);
    opacity: 0;
  }
}
.c-moon {
  position: absolute;
  width: 80vw;
  height: 80vw;
  top: 15vmin;
  border-radius: 50%;
  background: yellow;
  filter: blur(4px);
  transition: all 1s linear;
}
@media (min-width: 570px) {
  .c-moon {
    width: 35vmin;
    height: 35vmin;
    top: 15vmin;
  }
}
.c-moon__txt {
  position: absolute;
  width: auto;
  color: yellow;
  font-family: "Gluten", cursive;
  font-size: 2.5rem;
  line-height: 2;
  letter-spacing: 5px;
  top: 53%;
  right: 5%;
}
@media (min-width: 570px) {
  .c-moon__txt {
    font-size: min(3.5vmin, 2.5rem);
    bottom: 18vmin;
  }
}
.c-moon::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-color: transparent;
  border-radius: 50%;
  -webkit-filter: blur(100px);
  filter: blur(100px);
}
.c-moon__waning {
  width: 102%;
  height: 100%;
  margin: auto;
  border-radius: 50%;
  filter: blur(4px);
  overflow: hidden;
}
.c-moon__waning::before {
  content: "";
  display: block;
  width: 110%;
  height: 100%;
  border-radius: 50%;
  background: #670c67;
  transform: rotate(237deg) translateY(83px) translateX(29px);
  animation: moon-waning-sp 17s alternate-reverse infinite;
}
@media (min-width: 570px) {
  .c-moon__waning::before {
    animation: moon-waning 17s alternate-reverse infinite -1s;
  }
}
.c-moon img {
  position: absolute;
  top: 16%;
  width: 60%;
  opacity: 0.7;
}

@keyframes moon-waning-sp {
  0% {
    transform: rotate(227deg) translateY(340px) translateX(17px);
  }
  100% {
    transform: rotate(227deg) translateY(44px) translateX(17px);
  }
}
@keyframes moon-waning {
  0% {
    transform: rotate(227deg) translateY(272px) translateX(17px);
  }
  100% {
    transform: rotate(227deg) translateY(44px) translateX(17px);
  }
}
.c-nav {
  width: 0;
}
