  .c-nav {
    width: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    text-align: center;
  }
}
.c-nav li {
  cursor: pointer;
  width: 250px;
  position: absolute;
  width: 250px;
  right: -100vw;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 50px;
  filter: drop-shadow(2px 4px 6px);
  transition: 0.2s;
  transition-delay: calc(0.1s * var(--i));
  transition: transform 0.75s cubic-bezier(0.39, 1.52, 0.46, 0.92);
}
.c-nav li:nth-child(1) {
  top: 350px;
}
.c-nav li:nth-child(2) {
  top: 430px;
}
.c-nav li:nth-child(3) {
  top: 510px;
}
@media (min-width: 570px) {
  .c-nav li {
    position: static;
    width: auto;
    background: none;
    filter: none;
    border-radius: 0;
    animation: none;
  }
