}
@media (min-width: 570px) {
  .c-nav li a {
    font-size: 1.6rem;
  }
}

.c-overlay {
  position: fixed;
  height: 70px;
  width: 70px;
  background: radial-gradient(#fff, rgba(255, 255, 0, 0.8));
  transition: transform 0.6s cubic-bezier(1, 0, 0, 1);
  filter: blur(5px);
  z-index: -1;
  border-bottom-left-radius: 100%;
  top: -5px;
  right: -5px;
}

